.container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-family: 'Pacifico', cursive;
  height: 100vh;
  width: 100%;
}


.todolist-app{
  background: #02BFAC;
  width: 300px;
  height: 500px;
  margin: auto;
}

.todolist-header{
  position: absolute;
  width: 300px;
  height: 100px;
  background: #0CD0BD;
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 60%, 0% 100%);
          clip-path: polygon(0% 0%, 100% 0%, 100% 60%, 0% 100%);
}

.todolist-header h1{
  color: #FFF;
  font-size: 1.5em;
  padding: 1em;
}

.tasks{
  margin: 125px auto 0 auto;
  width: 80%;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #02BFAC), color-stop(90%, transparent), to(#02BFAC));
  background: -webkit-linear-gradient(top, #02BFAC 10%, transparent 90%, #02BFAC);
  background: -o-linear-gradient(top, #02BFAC 10%, transparent 90%, #02BFAC);
  background: linear-gradient(to bottom, #02BFAC 10%, transparent 90%, #02BFAC);
  height: 300px;
  overflow-y: scroll;
}

.tasks::-webkit-scrollbar{
  background: none;
  width: 5px;
}

.tasks::-webkit-scrollbar-track{
  background: none;
  width: 5px;
}

.tasks::-webkit-scrollbar-thumb{
  background: rgba(255,255,255,0.25);
  border-radius: 5px;
  width: 5px;
}


.tasks .task{
  min-height: 40px;
  border: none;
  background: #FFF;
  border-radius: 5px;
  font-family: 'Pacifico', cursive;
  font-size: 1em;
  text-align: center;
  padding: 5px 2px 5px 2px;
  -webkit-box-shadow: 0px 5px 7px 0px rgba(0,0,0,0.15);
          box-shadow: 0px 5px 7px 0px rgba(0,0,0,0.15);
  margin: 10px 2px 10px 2px;
  width: 230px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: all .1s;
  -o-transition: all .1s;
  transition: all .1s;
}

.tasks .task:hover{
  -webkit-transform: scale(0.95);
      -ms-transform: scale(0.95);
          transform: scale(0.95);
  cursor: pointer;
}


.todolist-footer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  -ms-flex-pack: center;
      justify-content: center;
  width: 300px;
  bottom: 90px;
  height: 100px;
  background: #0CD0BD;
  -webkit-clip-path: polygon(0% 30%, 100% 0%, 100% 100%, 0% 100%);
          clip-path: polygon(0% 30%, 100% 0%, 100% 100%, 0% 100%);
}

.todolist-footer form{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-top: 30px;
}

.todolist-footer form input{
  border: none;
  background: #FFF;
  border-radius: 7px;
  font-family: 'Pacifico', cursive;
  font-size: 1em;
  text-align: center;
  padding: 5px 2px 5px 2px;
}

.todolist-footer form button{
  border: none;
  background: #0B998B;
  width: 50px;
  height: 50px;
  border-radius: 7px;
  font-family: 'Pacifico', cursive;
  font-size: 1em;
  text-align: center;
  padding: 5px;
  color: #FFF;
  -webkit-transition: all .7s;
  -o-transition: all .7s;
  transition: all .7s;
}

.todolist-footer form button:hover{
  background: #05685E;
  cursor: pointer;
}

button:hover,button:focus, button:active{
  outline: none;
}

.quotes-app{
  color: #FFF;
  opacity: 0.62;
  margin: auto;
}



